.container-all i {
  margin-left: 1em;
}

.container-nav {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: fixed;
  width: 100%;
  z-index: 2;
  height: 4em;
}

.container-img {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-left: 8em;
}

.container-img img {
  width: 110px;
}

.container-items {
  display: flex;
  margin-right: 8em;
}

.item-nav {
  font-size: 20px;
  color: #a3a3a5;
  margin-left: 2em;
  display: flex;
  align-self: center;
  align-items: center;
}

label {
  display: none;
}

#toggle {
  display: none;
}

@media (max-width: 700px) {
  label {
    margin: 0px;
    height: 4rem;
    padding-left: 2em;
    display: flex;
    align-items: center;
    width: 100%;
    /* background-color: #e07a5f; */
    background-color: #ffffff;
  }
  .fa-bars:before {
    content: "\f0c9";
    color: #a3a3a5;
    font-size: 40px;
  }

  .container-img {
    display: none;
  }
  .container-items {
    display: flex;
    flex-direction: column;
    align-self: flex-start;
  }
  .item-nav {
    display: flex;
    padding: 5px;
    font-size: 18px;
    align-self: flex-start;
    margin-left: 1.5em;
    color: #000000;
  }

  #toggle:checked ~ .container-nav {
    display: flex;
    margin: 0;
    position: absolute;
    height: max-content;
  }

  .container-nav {
    display: none;
    width: 100%;
  }
}

.hvr-underline-from-center {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
  padding-bottom: 5px;
  cursor: pointer;
}
.hvr-underline-from-center:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 51%;
  right: 51%;
  bottom: 0;
  background: #e07a5f;
  height: 2px;
  -webkit-transition-property: left, right;
  transition-property: left, right;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-underline-from-center:active:before,
.hvr-underline-from-center:focus:before,
.hvr-underline-from-center:hover:before {
  left: 0;
  right: 0;
}
